/* Mobile */

@media (max-width: $width-mobile-max) {
    .hero-header {
        padding: 3em 1em 1em 1em;
        text-align: center;
    }
    .footer {
        text-align: center;
        padding: 40px 20px;
    }
    .footer__arrow {
        text-align: center;
        margin-top: 20px;
    }
}

/* iPad Portrait */

@media (min-width: $width-ipad-min) and (max-width: $width-ipad-max) {
    .hero-header {
        padding: 3em 1em 1em 1em;
        text-align: center;
    }
}

/* iPhone 5 and below */

@media (max-width: $width-iphone-5-max) {
    .hero-header h1 {
        font-size: 2em;
    }
    .hero-header h3 {
        font-size: 19px;
    }
}

/* iPhone 4 */
@media (max-width: $width-iphone-4-max) {
    .hero-header .logo {
        height: 110px;
    }
    .hero-header h1 {
        font-size: 2em;
    }
    .hero-header h3 {
        font-size: 19px;
    }
}
